import createApp from "@shopify/app-bridge";
import {
  Button,
  ButtonGroup,
  Modal,
  TitleBar,
  Toast
} from "@shopify/app-bridge/actions";

const app = createApp({
  apiKey: "app_bridge_key",
  shopOrigin: "shop1.myshopify.io"
});

console.log("app", app);

const toastOptions = {
  message: "Product saved",
  duration: "5000"
};
const toastNotice = Toast.create(app, toastOptions);

const button1 = Button.create(app, {
  label: "Show toast message"
});
button1.subscribe(Button.Action.CLICK, data => {
  console.log("Show toast message button clicked", data);
  toastNotice.dispatch(Toast.Action.SHOW);
});

const modalOptions = {
  title: "My Modal",
  message: "Hello world!"
};

const myModal = Modal.create(app, modalOptions);

const button2 = Button.create(app, { label: "Open modal" });
button2.subscribe(Button.Action.CLICK, data => {
  myModal.dispatch(Modal.Action.OPEN);
});

const moreActions = ButtonGroup.create(app, {
  label: "More actions",
  buttons: [button1, button2]
});

const saveButton = Button.create(app, { label: "Save" });
const titleBarOptions = {
  title: "My page title",
  buttons: {
    primary: saveButton,
    secondary: [moreActions]
  }
};

const myTitleBar = TitleBar.create(app, titleBarOptions);
